.headerContainer {

  height: 60px;
  background: #FCFCFC;
  box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 136px 0 136px;
  .button {
    background: #366EFF;
    border-radius: 30px;
  }
}

